.container {
  background-color: white;
}

.filter-blogs {
  margin-top: 20px;
  border-bottom: solid 2px black;
  padding: 5px;
}

.btn-new {
  float: right;
}

.table {
  max-width: 100%;
  margin-top: 5px;
}

table th {
  text-align: center;
}

table th + th {
  text-align: center;
}

table th + th + th {
  text-align: center;
}

table th + th + th + th {
  text-align: center;
}

table td {
  max-width: 60px !important;
  text-align: center !important;

  text-overflow: ellipsis !important;
  overflow: visible !important;
  white-space: nowrap !important;
  overflow: hidden !important;
}

table td + td {
  max-width: 280px !important;
  text-align: center !important;
}

table td + td + td {
  max-width: 320px !important;
  text-align: center;
}

table td + td + td + td {
  max-width: 130px !important;
  text-align: center !important;
}

.trash-icon {
  color: red;
}

.eye-icon {
  color: #138496;
}

.edit-icon {
  color: #218838;
}

.btn-trash {
  border: none;
  background: none;
}

.btn-search {
  background: #218838;
  border: none;
  color: white;
  font-size: 18px;
  border-radius: 3px;
}

.input-search {
  margin-left: 6px;
}

.select-pagination {
  width: 45px;
  border-color: #ced4da;
  border-radius: 4px;
}

.pagination {
  float: right;
}
